import React, { Component } from 'react';
import paiute from '../images/paiute.JPG';
import Features from './Features';
import Projects from './Projects';

export default class Jumbotron extends Component {
  render() {
    return (
      <div>

              <div className="card card-image pt-5 " style={{height: '600px' , backgroundImage: `url(${paiute})`}} >
                <div className="text-white text-center  py-5 px-4">
                    <div className="py-5">

                    
                    
                    <h1 className="card-title h2 my-4 py-2  ">Historic Courthouses</h1>
                    <div>
                            <a className="btn elegant-color"><i className="fa fa-clone left"></i> View gallery</a>
                            <a className="btn stylish-color"><i className="fa fa-clone left"></i> Contact Us</a>
                    </div>
                    </div>

                
                </div>
                </div>

                     <div className='container bob'>

                    <Projects />

                    <Features />

                    </div>
        
      </div>
    )
  }
}
