import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class Navigation extends Component {
  render() {
    return (
      <div>

            <nav className="navbar navbar-expand-lg navbar-dark elegant-color">
  <a className="navbar-brand text-white" >Historic Courthouses<i className="fa fa-institution ml-3" /> </a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav"
    aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarNav">
    <ul className="navbar-nav ml-auto">
      <li className="nav-item active">
      <h4> <Link to="/" className="nav-link">
 <i className="fa fa-home" /> Home
</Link></h4>
      </li>
      <li className="nav-item">
      <h4> <Link to="/gallery" className="nav-link">
 <i className="fa fa-camera-retro" /> Gallery
</Link></h4>
      </li>
      <li className="nav-item">
      <h4>  <Link to="/contact" className="nav-link">
 <i className="fa fa-send" /> Contact
</Link></h4>
      </li>
      
    </ul>
  </div>
</nav>
        
      </div>
    )
  }
}
