import React, { Component } from 'react'

export default class Contact extends Component {
  render() {
    return (
      <div>

          <div className='container'>

          
<section className="my-5">

 
  <h2 className="h1-responsive font-weight-bold text-center my-5">Contact us</h2>
  
  <p className="text-center w-responsive mx-auto mb-5">Lorem ipsum dolor sit amet, consectetur adipisicing elit.
    Fugit, error amet numquam iure provident voluptate esse quasi, veritatis totam voluptas nostrum quisquam
    eum porro a pariatur veniam.</p>

  

  <div className="row">

    
    <div className="col-md-9 mb-md-0 mb-5">

      <form>

        
        <div className="row">

          
          <div className="col-md-6">
            <div className="md-form mb-0">
              <input type="text" id="contact-name" className="form-control" />
              <label htmlFor="contact-name" className="">Your name</label>
            </div>
          </div>
          

          
          <div className="col-md-6">
            <div className="md-form mb-0">
              <input type="text" id="contact-email" className="form-control" />
              <label htmlFor="contact-email" className="">Your email</label>
            </div>
          </div>
          

        </div>
        

        
        <div className="row">

          
          <div className="col-md-12">
            <div className="md-form mb-0">
              <input type="text" id="contact-Subject" className="form-control" />
              <label htmlFor="contact-Subject" className="">Subject</label>
            </div>
          </div>
          

        </div>
        

        
        <div className="row">

          
          <div className="col-md-12">
            <div className="md-form">
              <textarea type="text" id="contact-message" className="form-control md-textarea" rows="3"></textarea>
              <label htmlFor="contact-message">Your message</label>
            </div>
          </div>
          

        </div>
        

      </form>

      <div className="text-center text-md-left">
        <a className="btn btn-elegant btn-lg">Send</a>
      </div>

    </div>
    

    
    <div className="col-md-3 text-center">
      <ul className="list-unstyled mb-0">
        <li>
          <i className="fa fa-map-marker fa-2x black-text"></i>
          <p>Salt Lake City, UT 94126, USA</p>
        </li>
        <li>
          <i className="fa fa-phone fa-2x mt-4 black-text"></i>
          <p>+ 01 234 567 89</p>
        </li>
        <li>
          <i className="fa fa-envelope fa-2x mt-4 black-text"></i>
          <p className="mb-0">contact@example.com</p>
        </li>
      </ul>
    </div>
    

  </div>
  

</section>

</div>

        
      </div>
    )
  }
}
